import React from 'react';
import { FormControl } from "../FormControl/FormControl";
import { Image } from "../Image/Image";

export const Gallery = (props) => {


  return (
    <div id='portfolio' className='container-fluid'>
      <div className='container'>
        <div className='section-title' style={{textAlign:'center'}}>
          <h2>{props.t('Каталог')}</h2>
          <p>
            {props.t('Каталог продукции')}
          </p>
        </div>
        <div className='row'>
          <div className='col-md-8' style={{borderRight:'solid 0.1px', borderColor:'#d5d5d5'}}>
            {props.data
              ? props.data.map((d, i) => (
                <div key={`${d.title}-${i}`} className='col-sm-6 col-md-4 col-lg-2 img-container '>
                  <Image title={d.title} largeImage={d.largeImage} smallImage={d.smallImage} />
                </div>
              ))
              : 'Loading...'
            }
          </div>
          <div className='col-md-4'>
            <h3 style={{textAlign:'center'}}>{props.t('ЗАПРОСИТЬ ПРАЙС')}</h3>
            <FormControl t={props.t}/>
          </div>
        </div>
        <div>
          <a 
            className="btn btn-custom btn-lg page-scroll"
            href='/files/КАТАЛОГ_ДиКом_2022.pdf' download
            style={{marginRight:'3px'}}>
            <i className="fa fa-download"></i>
          </a>
          <a href='/files/КАТАЛОГ_ДиКом_2022.pdf' download
              className='btn btn-custom btn-lg page-scroll'>
              {props.t('Скачать Каталог в формате PDF')}
          </a> {' '}
        </div>
        <br/>
        <div className='col-md-8'>
          <h4>{props.t('Вы также можете ознакомиться с нашей продукцией на нашем российском сайте')}
            <span> </span>
            <a href="https://ppkdikom.ru/" target={'blank'} style={{color:"green", textDecoration:'underline'}}><i>ppkdikom.ru</i></a>
          </h4>
        </div>   
      </div>
    </div>
  )
}







